/**
 * Calculates offset coordinate for each element array.
 * @example calcLeftOffsets([200, 300, 400]) // [(200 / 2), (200 + 300 / 2), (200 + 300 + 400 / 2)] => [100, 350, 700];
 *
 * @param {Array<Number>} elementsWidthArray Array representing actual width of each element. e.i. [200, 205, 300, ...]
 * @returns Array
 */
export const calcLeftOffsets = (elementsWidthArray: number[]) => {
  const start: number[] = [];

  return elementsWidthArray.reduce((prev, curr, index, arr) => {
    let base;
    if (index === 0) {
      return [curr / 2];
    } else {
      const total = arr.slice(0, index).reduce((prev, curr) => prev + curr, 0);
      base = total + arr[index] / 2;
    }
    return [...prev, base];
  }, start);
};
